import React, { useEffect, useRef, useState, useContext } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import Drift from "drift-zoom";
import "drift-zoom/dist/drift-basic.css";
import { useParams } from "react-router";
import { apiValue } from "../Data/AllData";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import {  ShoppingCartContext } from "../CartShoppingContext/ShoppingCart";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer/Footer";






function AllProductDetails() {
  const { id } = useParams();
  const data = useContext(apiValue);
  const navigate = useNavigate();
  const [quantity, setQuantity] = useState(1);
  const MIN_QUANTITY = 1;

  const { cartItems, addItem, updateItemQuantity } = useContext(ShoppingCartContext);



  const handleDecrease = () => {
    setQuantity((prev) => Math.max(prev - 1, MIN_QUANTITY));
  };

  const handleIncrease = () => {
    setQuantity((prev) => Math.min(prev + 1, MAX_QUANTITY));
  };

  const handleQuantityChange = (e) => {
    const value = parseInt(e.target.value, 10);
    if (!isNaN(value)) {
      setQuantity(Math.min(Math.max(value, MIN_QUANTITY), MAX_QUANTITY));
    }
  };

  const handleAddToCart = () => {
    if (quantity > MAX_QUANTITY) {
      alert(`     ${MAX_QUANTITY}    `);
      return;
    }
    addItem(product, quantity); //   
    alert(`${quantity} item(s) have been added to the cart `);
  };










  const handleBuyNow = () => {
    const existingItem = cartItems.find(item => item.id === product.id);

    if (existingItem) {
      //         
      updateItemQuantity(product.id, existingItem.quantity);
    } else {
      //        
      addItem(product, quantity);
    }

    navigate("/checkout");
  };







  const handleAddToWishlist = () => {
    alert("Successfully added to wishlist ");
  };

  //    
  const product =
    data && data.length > 0
      ? data.find((item) => String(item.id) === String(id)) || data[0]
      : null;

  


  const MAX_QUANTITY = product?.stock || 1;



  




  


 


  const [activeImage, setActiveImage] = useState("/assets/img/product/product-details-6.webp");

  //    (    )
  const images =
    product && product.images && product.images.length > 0
      ? product.images
      : ["/assets/img/product/product-details-6.webp"];

  //        
  useEffect(() => {
    if (product && product.images && product.images.length > 0) {
      setActiveImage(product.images[0]);
    } else {
      setActiveImage("/assets/img/product/product-details-6.webp");
    }
  }, [product]);


  //     

  const mainImageRef = useRef(null);
  const driftRef = useRef(null);

  //    + 
  useEffect(() => {
    AOS.init({ duration: 800, once: true });

    if (mainImageRef.current) {
      if (driftRef.current) driftRef.current.destroy();
      driftRef.current = new Drift(mainImageRef.current, {
        paneContainer: mainImageRef.current.parentElement,
        inlinePane: false,
        containInline: true,
        zoomFactor: 2,
      });
    }
  }, [activeImage]);

  //   
  if (!data || data.length === 0 || !product) {
    return (
      <div>
        <Navbar />
        <div className="text-center mt-5">Loading...</div>
        <Footer />
      </div>
    );
  }

  return (
    <div>
      <Navbar/>
      <br />
      <br />
      <br />
      <main className="main">
        {/* Page Title */}
        <div className="page-title light-background">
          <div className="container d-lg-flex justify-content-between align-items-center">
            <h1 className="mb-2 mb-lg-0">{product.name || "Product Details"}</h1>
            <nav className="breadcrumbs">
              <ol>
                <li><Link to="/">Home</Link></li>
                <li className="current">Product Details</li>
              </ol>
            </nav>
          </div>
        </div>

        {/* Product Details Section */}
        <section id="product-details" className="product-details section">
          <div className="container" data-aos="fade-up" data-aos-delay={100}>
            <div className="row g-4">
              {/*    */}
              <div className="col-lg-7" data-aos="zoom-in" data-aos-delay={150}>
                <div className="product-gallery">
                  <div className="main-showcase">
                    <div className="image-zoom-container">
                      <img
                        ref={mainImageRef}
                        src={activeImage}
                        alt="Product Main"
                        className="img-fluid main-product-image drift-zoom"
                        data-zoom={activeImage}
                      />

                      {/*    */}
                      <div className="image-navigation">
                        <button
                          className="nav-arrow prev-image"
                          onClick={() => {
                            const currentIndex = images.indexOf(activeImage);
                            setActiveImage(
                              images[
                              (currentIndex - 1 + images.length) % images.length
                              ]
                            );
                          }}
                        >
                          <i className="bi bi-chevron-left" />
                        </button>
                        <button
                          className="nav-arrow next-image"
                          onClick={() => {
                            const currentIndex = images.indexOf(activeImage);
                            setActiveImage(
                              images[(currentIndex + 1) % images.length]
                            );
                          }}
                        >
                          <i className="bi bi-chevron-right" />
                        </button>
                      </div>
                    </div>
                  </div>

                  {/*    Swiper */}
                  <Swiper
                    modules={[Navigation, Pagination]}
                    spaceBetween={10}
                    slidesPerView={4}
                    navigation
                    pagination={{ clickable: true }}
                    className="thumbnail-grid"
                  >
                    {images.map((img, i) => (
                      <SwiperSlide key={i}>
                        <div
                          className={`thumbnail-wrapper thumbnail-item ${activeImage === img ? "active" : ""
                            }`}
                          onClick={() => setActiveImage(img)}
                        >
                          <img
                            src={img}
                            alt={`View ${i + 1}`}
                            className="img-fluid"
                          />
                        </div>
                      </SwiperSlide>
                    ))}
                  </Swiper>
                </div>
              </div>

              {/*     */}
              <div className="col-lg-5 product-details">
                <h2>{product.title}</h2>
                <p>{product.description}</p>
                <p style={{ color: "gray" }}>{product.brand ? `Brand: ${product.brand}` : null}</p>
                <div className="pricing-section"><div className="price-display"><span className="sale-price">Price: {product.price} EGP</span><span className="regular-price">EGP {product.discountPercentage}</span></div></div>
                <div className="availability-status"><div className="stock-indicator"><i className="bi bi-check-circle-fill"></i><span className="stock-text">Available</span></div><div className="quantity-left">Only {product.stock} items remaining</div></div>
                <div className="purchase-section">
                  <div className="quantity-control">
                    <label className="control-label">Quantity:</label>
                    <div className="quantity-input-group">
                      <div className="quantity-selector">
                        <button className="quantity-btn decrease" type="button" onClick={handleDecrease}>
                          <i className="bi bi-dash" />
                        </button>
                        <input
                          type="number"
                          className="quantity-input"
                          value={quantity}
                          min={MIN_QUANTITY}
                          max={MAX_QUANTITY}
                          onChange={handleQuantityChange}
                        />
                        <button className="quantity-btn increase" type="button" onClick={handleIncrease}>
                          <i className="bi bi-plus" />
                        </button>
                      </div>
                    </div>
                  </div>

                  <div className="action-buttons">
                    <button className="btn primary-action" onClick={handleAddToCart}>
                      <i className="bi bi-bag-plus" />
                      Add to Cart
                    </button>
                    <button className="btn secondary-action" onClick={handleBuyNow}>
                      <i className="bi bi-lightning" />
                      Buy Now
                    </button>
                    <button className="btn icon-action" title="Add to Wishlist" onClick={handleAddToWishlist}>
                      <i className="bi bi-heart" />
                    </button>
                  </div>
                  <br />
                  <Link to="/Products" className="btn btn-outline-danger btn-block w-100 text-center">
                    Continue Shopping
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
     <Footer/>
    </div>
  );
}

export default AllProductDetails;
