import React, { createContext, useState, useEffect } from "react";


export const ShoppingCartContext = createContext({});
const initialCartItems = localStorage.getItem("cart-shopping") ? JSON.parse(localStorage.getItem("cart-shopping"))
  : [];


export const ShoppingCart = ({ children }) => {
  const [cartItems, setCartItems] = useState(initialCartItems);

  useEffect(() => {
    localStorage.setItem("cart-shopping", JSON.stringify(cartItems));
  }, [cartItems]);

  const addItem = (product, quantity = 1) => {
    setCartItems(prev => {
      const existing = prev.find(item => item.id === product.id);
      if (existing) {
        return prev.map(item =>
          item.id === product.id
            ? { ...item, quantity: Math.min(item.quantity + quantity, product.stock || Infinity) }
            : item
        );
      } else {
        return [...prev, { ...product, quantity }];
      }
    });
  };

  const removeItem = id => setCartItems(prev => prev.filter(item => item.id !== id));
  const updateItemQuantity = (id, quantity) =>
    setCartItems(prev => prev.map(item => (item.id === id ? { ...item, quantity } : item)));
  const emptyCart = () => setCartItems([]);

  const totalItems = cartItems.reduce((acc, item) => acc + item.quantity, 0);
  const totalUniqueItems = cartItems.length;
  const cartTotal = cartItems.reduce((acc, item) => acc + item.price * item.quantity, 0);

  return (
    <ShoppingCartContext.Provider
      value={{
        cartItems,
        addItem,
        removeItem,
        updateItemQuantity,
        emptyCart,
        totalItems,
        totalUniqueItems,
        cartTotal
      }}
    >
      {children}
    </ShoppingCartContext.Provider>
  );
};
