import React from 'react'
import { NavLink } from 'react-router-dom'

function Navbar() {
    return (
        <>
            <header id="header" className="header sticky-top">

                {/* Top Bar */}
                <div className="top-bar py-2">
                    <div className="container-fluid container-xl">
                        <div className="row align-items-center">
                            <div className="col-lg-4 d-none d-lg-flex">
                                <div className="top-bar-item">
                                    <i className="bi bi-telephone-fill me-2" />
                                    <span>Need help? Call us: </span>
                                    <NavLink to="tel:+1234567890">+1 (234) 567-890</NavLink>
                                </div>
                            </div>
                            <div className="col-lg-4 col-md-12 text-center">
                                <div className="announcement-slider swiper init-swiper">
                                    <div className="swiper-wrapper">
                                        <div className="swiper-slide">
                                             Free shipping on orders over $50
                                        </div>
                                        <div className="swiper-slide">
                                             30 days money back guarantee.
                                        </div>
                                        <div className="swiper-slide"> 20% off on your first order</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-4 d-none d-lg-block">
                                <div className="d-flex justify-content-end">
                                    <div className="top-bar-item dropdown me-3">
                                        <NavLink to="#" className="dropdown-toggle" data-bs-toggle="dropdown">
                                            <i className="bi bi-translate me-2" />
                                            EN
                                        </NavLink>
                                        <ul className="dropdown-menu">
                                            <li>
                                                <NavLink className="dropdown-item" to="#">
                                                    <i className="bi bi-check2 me-2 selected-icon" />
                                                    English
                                                </NavLink>
                                            </li>
                                            <li>
                                                <NavLink className="dropdown-item" to="#">
                                                    Espaol
                                                </NavLink>
                                            </li>
                                            <li>
                                                <NavLink className="dropdown-item" to="#">
                                                    Franais
                                                </NavLink>
                                            </li>
                                            <li>
                                                <NavLink className="dropdown-item" to="#">
                                                    Deutsch
                                                </NavLink>
                                            </li>
                                        </ul>
                                    </div>
                                    <div className="top-bar-item dropdown">
                                        <NavLink to="#" className="dropdown-toggle" data-bs-toggle="dropdown">
                                            <i className="bi bi-currency-dollar me-2" />
                                            USD
                                        </NavLink>
                                        <ul className="dropdown-menu">
                                            <li>
                                                <NavLink className="dropdown-item" to="#">
                                                    <i className="bi bi-check2 me-2 selected-icon" />
                                                    USD
                                                </NavLink>
                                            </li>
                                            <li>
                                                <NavLink className="dropdown-item" to="#">
                                                    EUR
                                                </NavLink>
                                            </li>
                                            <li>
                                                <NavLink className="dropdown-item" to="#">
                                                    GBP
                                                </NavLink>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Main Header */}
                <div className="main-header">
                    <div className="container-fluid container-xl">
                        <div className="d-flex py-3 align-items-center justify-content-between">
                            {/* Logo */}
                            <NavLink to="index.html" className="logo d-flex align-items-center">
                                {/* Uncomment the line below if you also wish to use an image logo */}
                                {/* <img src="assets/img/logo.webp" alt=""> */}
                                <h1 className="sitename">NiceShop</h1>
                            </NavLink>

                            {/* Actions */}
                            <div className="header-actions d-flex align-items-center justify-content-end">
                                {/* Account */}
                                <div className="dropdown account-dropdown">
                                    <button className="header-action-btn" data-bs-toggle="dropdown">
                                        <i className="bi bi-person" />
                                    </button>
                                    <div className="dropdown-menu">
                                        <div className="dropdown-header">
                                            <h6>
                                                Welcome to <span className="sitename">FashionStore</span>
                                            </h6>
                                            <p className="mb-0">Access account &amp; manage orders</p>
                                        </div>
                                        <div className="dropdown-body">
                                            <NavLink
                                                className="dropdown-item d-flex align-items-center"
                                                to="account.html"
                                            >
                                                <i className="bi bi-person-circle me-2" />
                                                <span>My Profile</span>
                                            </NavLink>
                                            <NavLink
                                                className="dropdown-item d-flex align-items-center"
                                                to="account.html"
                                            >
                                                <i className="bi bi-bag-check me-2" />
                                                <span>My Orders</span>
                                            </NavLink>
                                            <NavLink
                                                className="dropdown-item d-flex align-items-center"
                                                to="account.html"
                                            >
                                                <i className="bi bi-heart me-2" />
                                                <span>My Wishlist</span>
                                            </NavLink>
                                            <NavLink
                                                className="dropdown-item d-flex align-items-center"
                                                to="account.html"
                                            >
                                                <i className="bi bi-gear me-2" />
                                                <span>Settings</span>
                                            </NavLink>
                                        </div>
                                        <div className="dropdown-footer">
                                            <NavLink to="register.html" className="btn btn-primary w-100 mb-2">
                                                Sign In
                                            </NavLink>
                                            <NavLink to="login.html" className="btn btn-outline-primary w-100">
                                                Register
                                            </NavLink>
                                        </div>
                                    </div>
                                </div>
                                {/* Wishlist */}
                                <NavLink
                                    to="account.html"
                                    className="header-action-btn d-none d-md-block"
                                >
                                    <i className="bi bi-heart" />
                                    <span className="badge">0</span>
                                </NavLink>
                                {/* Cart */}
                                <NavLink to="cart.html" className="header-action-btn">
                                    <i className="bi bi-cart3" />
                                    <span className="badge">3</span>
                                </NavLink>
                                {/* Mobile Navigation Toggle */}
                                <i className="mobile-nav-toggle d-xl-none bi bi-list me-0" />
                            </div>
                        </div>
                    </div>
                </div>


                {/* Navigation */}
                <div className="header-nav">
                    <div className="container-fluid container-xl position-relative">
                        <nav id="navmenu" className="navmenu">

                            <ul>

                                <li>
                                    <NavLink to="/" className="active">
                                        Home
                                    </NavLink>
                                </li>

                                <li>
                                    <NavLink to="#">About</NavLink>
                                </li>

                                <li>
                                    <NavLink to="/Products">Products</NavLink>
                                </li>

                                <li>
                                    <NavLink to="cart.html">Cart</NavLink>
                                </li>

                                <li>
                                    <NavLink to="/Checkout">Checkout</NavLink>
                                </li>


                                <li>
                                    <NavLink to="/AllContact">Contact</NavLink>
                                </li>

                            </ul>

                        </nav>

                    </div>
                </div>

                {/* Mobile Search Form */}
                <div className="collapse" id="mobileSearch">
                    <div className="container">
                        <form className="search-form">
                            <div className="input-group">
                                <input
                                    type="text"
                                    className="form-control"
                                    placeholder="Search for products"
                                />
                                <button className="btn" type="submit">
                                    <i className="bi bi-search" />
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            </header>
        </>
    )
}

export default Navbar