import ReactDOM from 'react-dom/client';
import App from './App';
import { createHashRouter, RouterProvider, } from "react-router-dom";
import { AllData } from './Data/AllData';
import ScrollToTop from 'react-scroll-to-top';
import AllContact from './Contact/AllContact';
import AllAccount from './Account/AllAccount';
import AllSupport from './components/Footer/Support/AllSupport';
import AllTerms from './components/Footer/Tos/AllTerms';
import AllPrivacy from './components/Footer/privacy/AllPrivacy';
import AllProductDetails from './ProductDetails/AllProductDetails';
import AllProducts from "./Products/AllProducts"
import AllCheckout from './Checkout/AllCheckout';
import { CartProvider } from 'react-use-cart';
import { ShoppingCart } from './CartShoppingContext/ShoppingCart';



let router = createHashRouter([
  {
    path: "/",
    element: <App />
  },
  {
    path: "AllContact",
    element: <AllContact />
  },
  {
    path: "AllAccount",
    element: <AllAccount />
  },
  {
    path: "AllSupport",
    element: <AllSupport />
  },
  {
    path: "AllTerms",
    element: <AllTerms />
  },
  {
    path: "AllPrivacy",
    element: <AllPrivacy />
  },
  {
    path: "ProductDetails/:id",
    element: <AllProductDetails />
  },
  {
    path: "Products",
    element: <AllProducts />
  },
  {
    path: "Checkout",
    element: <AllCheckout />
  },
]);

ReactDOM.createRoot(document.getElementById("root")).render(
  <AllData>
    <CartProvider>
      <ShoppingCart>
        <RouterProvider router={router} />
        <ScrollToTop smooth />
      </ShoppingCart>
    </CartProvider>
  </AllData>
);





